<template>
  <div class="cont">
    <Mheader :show=true>
      <div slot="title">短信登录</div>
    </Mheader>
    <div class="box">
      <div class="msg-login">
        <img src="../../assets/images/login&register/login_03.png"/>
      </div>
      <div class="msg-login-box">
        <mt-field placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
        <div class="code">
          <mt-field placeholder="请输入验证码" type="number" v-model="number">
          </mt-field>
          <div :class="fetchCodeMsg?'seconds':'seconding'"   @click="sendCode">{{timerCodeMsg}}</div>
        </div>
        <div class="msg-login-btn" @click="smsLogin">登录</div>
        <div class="methods">
          <div class="m-pswd">
            <router-link :to="{path:'/login'}">
              <img src="../../assets/images/login&register/password.png" />
              密码登录
            </router-link>
          </div>
          <div class="m-phone">
            <router-link :to="{path:'/Register'}">
              <img src="../../assets/images/login&register/phone.png" />
              快速注册
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Mheader from '../../components/Mheader'
  import Mdialog from '../../components/Mdialog'
  //import { MessageBox } from 'mint-ui';
   import { Toast } from 'mint-ui';

  export default {
    components: {
      Mheader,
      Mdialog
    },
    data() {
      return {
        phone: null,
        number: null,
        timerCodeMsg:'获取验证码',
        fetchCodeMsg:false 
      }
    },
    methods: {
      sendCode(){//发送短信验证码  
          if(!!!this.phone){
           Toast('手机号不能为空');
            return;
          } 
          if(!this.isPhoneNo(this.phone)){
             Toast('手机号格式不正确');
             return;
          }      
          this.axios.post(this.url+'/api/Login/SendSMSCode',{phone:this.phone}).then((res)=>{
            this.timeOut();//发送成功开始倒计时
            if(res.data.Code==200){
              Toast(res.data.Data);
            }else{
              Toast(res.data.Data);
            }
          }).catch((err)=>{
             Toast('网络请求超时');
          })
      }, 
      timeOut(){//倒计时
        let self=this;
        self.fetchCodeMsg = true
        let sec =60;
        for(let  i=0; i<=60; i++){
          window.setTimeout(function(){
              if (sec != 0) {                
                self.timerCodeMsg = sec + "s后重新发送" ;
                sec--;
            } else {
                sec = 60;//如果倒计时结束就让重新获取验证码显示出来
                self.timerCodeMsg="重新获取验证码";
                self.fetchCodeMsg = false
            }
          }, i * 1000)
        }
      },
      verifyPassword(pwd){//密码验证
          let pattern=/^[A-Za-z_0-9]{6,16}$/;
          return pattern.test(pwd);
      },
      isPhoneNo(phone) {  //手机号验证 
        var pattern = /^1[34578]\d{9}$/; 
        return pattern.test(phone); 
      },
      smsLogin(){//短信登录
          if(!!!this.phone){
          Toast('手机号不能为空');
          return;
          } 
          if(!this.isPhoneNo(this.phone)){
              Toast('手机号格式不正确');
              return;
          }  
          if(!!!this.number){
            Toast('验证码不能为空');
            return;
          }
          if(this.number.length !=6){
            Toast('验证码格式不正确');
            return;
          }           
          this.axios.post(this.url+'/api/Login/SMSLogin',{phone:this.phone,code:this.number}).then((res)=>{
            if(res.data.Code==200){
              let instance = Toast(res.data.Data);
              setTimeout(() => {
                instance.close();
                //TODO:登录跳转从那个页面来 回那个页面还没做
                this.$router.push({ path: '/home' })
              }, 1000);
            
            }else{
              Toast(res.data.Data);
            }
          }).catch((err)=>{
            Toast('网络请求超时');
          })  
      },

    }
  }
</script>

<style scoped>
  .cont{
    height: 100vh;
    background-color: #ffffff;
  }
  .box {
    font-size: 0.7rem;
  }

  .box .msg-login-box {
    padding: 0 1.5em 1rem;
  }

  .box .msg-login {
    height: 9rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .box .msg-login > img {
    margin: 0 auto;
    width: 6rem;
    height: 4.0rem;
  }

  .box .msg-login-box .msg-login-btn {
    margin-top: 0.8rem;
    text-align: center;
    color: #fff;
    padding: 0.5rem 0;
    border-radius: 0.2rem;
    background-color: #B4282D;
  }

  .methods{
    margin-top: 0.4rem;
    font-size: 0.65rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .methods  a{
    display: flex;
    align-items: center;
  }
  .methods  a > img{
    margin-right: 0.2rem;
    width: 0.7rem;
    height: 0.7rem;
  }
  .methods .m-msg a > img{
    width: 0.9rem;
    height: 0.9rem;
  }
  .code{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .code .seconds{
    height: 2.1rem;
    width: 5.5rem;
    color: #A9A9A9;
    text-align: center;
    line-height: 2.1rem;
    border: 1px solid #d9d9d9;
    border-radius: 0.2rem;
    margin-top: 0.8rem;
    pointer-events: none;
  }
   .code .seconding{
    height: 2.1rem;
    width: 5.5rem;
    color: #B4282D;
    text-align: center;
    line-height: 2.1rem;
    border: 1px solid #B4282D;
    border-radius: 0.2rem;
    margin-top: 0.8rem;    
  }
  .code a{

    width: 7.5rem!important;
  }
</style>



